.tooltip {
  padding: 10px;
  font-size: 11px;
  line-height: 17px;
  letter-spacing: 0.04em;
  text-align: center;
  background-color: rgba(29, 33, 41, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  transition: opacity 300ms ease-in;
  color: white;
  opacity: 0;
  max-width: calc(100% - 30px);
  z-index: 2000;
  white-space: initial;
  word-break: break-word;
}
.tooltip.shown {
  opacity: 1;
}

.tooltip-menu {
  display: block;
  position: absolute;
  inset: 0px auto auto 0px;
  margin: 0px;
  transform: translate(162px, 184px);
  padding: 10px 0;
  border: none;
  background: white;
  z-index: 2;
  box-shadow: rgba(20, 28, 98, 0.1) 0px 5px 20px;
  border-radius: 3px;
  min-width: 150px;
  font-size: 14px;
}
.tooltip-menu-item {
  display: block;
  padding: 15px 20px;
